body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.masonry-container {
  width: 80%;
  margin: auto;
  margin-top: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.masonry-container p {
  text-align: center;
  padding: 4px;
  font-family: monospace;
  background: #222;
  color: #efefef;
}

.masonry {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  width: 100%;
  margin: auto;
}

.column {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-line-pack: stretch;
      align-content: stretch;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.tile {
  margin: 4px;
}
.tile img {
  -webkit-box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.15);
          box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  width: 100%;
}


